@import "../../../../components/theme/mixin";

.drawer-toggle {
  .icon-pure-menu {
    @include theme-prop(color, secondary-on-dark);
  }
  &:hover {
    .icon-pure-menu {
      @include theme-prop(color, primary-on-dark);
    }
  }
}



